import styled, { keyframes } from "styled-components";

const IntroFooter = () => {
  return (
    <>
      <IntroFooterContainer>
        I'm a recent graduate of the{" "}
        <span style={{ color: "#b382dd" }}>
          Business Information Technology
        </span>{" "}
        program at Red River College.
        <IntroFooterCaption>
          Where I majored in Web and Full-stack Development.
        </IntroFooterCaption>
      </IntroFooterContainer>
    </>
  );
};

const slideRight = keyframes`
  from {
    margin-right: -100%;
    width: 100%;
  }

  to {
    margin-right: 0;
    width: 100%;
  }
}
`;

const IntroFooterContainer = styled.div`
  margin-left: auto;
  color: #553285;
  font-family: "EB Garamond", serif;
  font-size: 2.5em;
  max-width: 750px;
  animation-name: ${slideRight};
  animation-duration: 4s;
  animation-iteration-count: 1;

  @media (max-width: 1030px) {
    padding: 1em 0;
  }
  @media (max-width: 450px) {
    padding: 1em 0;
    font-size: 2em;
  }
`;

const IntroFooterCaption = styled.div`
  color: #b382dd;
  font-family: "Comfortaa", cursive;
  font-size: 0.3em;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding-top: 1.8em;
  line-height: 2;
`;

export default IntroFooter;
