import styled from "styled-components";
import { useState } from "react";
import { send } from "emailjs-com";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css";

const ContactForm = () => {
  const [toSend, setToSend] = useState({
    from_name: "",
    message: "",
    reply_to: "",
  });

  const [popupVisible, setPopupVisible] = useState(false);

  const onSubmit = (e) => {
    e.preventDefault();
    send(
      "service_93a9xat",
      "template_rrqf3ar",
      toSend,
      "user_zjXYo8yA5XPUpBOdErcvv"
    )
      .then((response) => {
        console.log("SUCCESS!", response.status, response.text);
        setToSend({ from_name: "", message: "", reply_to: "" });
      })
      .then(() => {
        confirmAlert({
          title: "Your email was sent!",
          message: "Thank you!",
          buttons: [
            {
              label: "Close",
            },
          ],
        });
      })
      .catch((err) => {
        console.log("FAILED...", err);
      });
  };

  const handleChange = (e) => {
    setToSend({ ...toSend, [e.target.name]: e.target.value });
  };

  return (
    <>
      <ContactFormContainer>
        <form onSubmit={onSubmit}>
          <FormObject>
            <FormText>Name:</FormText>
            <FormInput>
              <input
                type="text"
                name="from_name"
                value={toSend.from_name}
                onChange={handleChange}
                style={{
                  backgroundColor: "#b382dd",
                  border: 0,
                  color: "#25064d",
                  fontFamily: "EB Garamond",
                  fontSize: "1em",
                  marginLeft: "5px",
                  width: "97%",
                }}
              />
            </FormInput>
            <FormText>Email:</FormText>
            <FormInput>
              <input
                type="text"
                name="reply_to"
                value={toSend.reply_to}
                onChange={handleChange}
                style={{
                  backgroundColor: "#b382dd",
                  border: 0,
                  color: "#25064d",
                  fontFamily: "EB Garamond",
                  fontSize: "1em",
                  marginLeft: "5px",
                  width: "97%",
                }}
              />
            </FormInput>
          </FormObject>
          <FormObject>
            <FormText>Message:</FormText>
            <FormInput>
              <textarea
                name="message"
                value={toSend.message}
                onChange={handleChange}
                style={{
                  backgroundColor: "#b382dd",
                  border: 0,
                  color: "#25064d",
                  fontFamily: "EB Garamond",
                  fontSize: "1em",
                  marginLeft: "5px",
                  width: "99%",
                }}
              />
            </FormInput>
          </FormObject>
          <SubmitButton type="submit">Submit</SubmitButton>
        </form>
      </ContactFormContainer>
    </>
  );
};

export default ContactForm;

const ContactFormContainer = styled.div`
  font-size: 0.5em;
  color: #b382dd;
  font-family: "Comfortaa", cursive;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding: 1em 0;
`;

const FormObject = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.4em 0;
  @media (max-width: 600px) {
    align-items: flex-start;
    flex-direction: column;
    padding: 0;
  }
`;

const FormInput = styled.div`
  width: 97%;
  @media (max-width: 600px) {
    padding: 0.4em 0;
    width: 98%;
  }
`;

const FormText = styled.div`
  padding: 0.4em 0;
`;

const SubmitButton = styled.button`
  width: 100%;
  padding: 0.5em 0.5em;
  margin-top: 0.5em;
  border: 1px solid #b382dd;
  background-color: #25064d;
  color: #b382dd;

  &:hover {
    cursor: pointer;
    background-color: #b382dd;
    color: #25064d;
  }

  @media (max-width: 600px) {
    padding: 0.4em 1em;
  }
`;
