import styled from "styled-components";
import { useState } from "react";

const Project = (props) => {
  const [modal, setModalValue] = useState(true);

  const setModal = () => {
    setModalValue(!modal);
    showModal();
  };

  const showModal = () => {
    if (modal === true) return "inline";
    return "none";
  };

  return (
    <>
      <ProjectBackground
        key={props.name}
        style={{
          backgroundColor: "black",
          backgroundImage: `url(${props.thumbnail})`,
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
        }}
      >
        <ProjectForeground
          style={{ backgroundColor: "rgba(0, 0, 0, 0.2)" }}
          onClick={() => setModal()}
        >
          {props.name}
          <div style={{ fontSize: ".6em", lineHeight: "2em" }}>
            {props.technologies}
          </div>
          <Icons>
            {props.demo ? (
              <LinkColour href={props.demo}>
                <i className="fas fa-link"></i>
              </LinkColour>
            ) : null}
            {props.link ? (
              <LinkColour href={props.link}>
                <i className="fab fa-github fa-lg"></i>
              </LinkColour>
            ) : null}
          </Icons>
          <ProjectModal style={{ display: () => showModal() }}>
            Hi.
          </ProjectModal>
        </ProjectForeground>
      </ProjectBackground>
    </>
  );
};

const ProjectBackground = styled.div`
  color: white;
  min-width: 330px;
  min-height: 300px;
  margin: 10px 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.25em;
  opacity: 0.35;

  &:hover {
    opacity: 1;
    cursor: pointer;
  }
`;

const ProjectForeground = styled.div`
  height: 290px;
  width: 320px;
  padding: 10px 10px 0px 10px;
`;

const ProjectModal = styled.div`
  width: 100%;
  height: 100%;
  background-color: white;
  display: none;
`;

const Icons = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  position: relative;
  bottom: -225px;
  padding-left: 275px;
`;

const LinkColour = styled.a`
  display: block;
  color: white;
  padding: 3px;
`;

export default Project;
