import styled from "styled-components";
import { SocialMediaLinks } from "../info";
import NavSocialMedia from "../components/NavSocialMedia";

const Navbar = () => {
  return (
    <>
      <NavTitle>Chantal Wiebe</NavTitle>
      <NavSocialMediaContainer>
        {SocialMediaLinks.map((sm) => {
          return <NavSocialMedia link={sm.link} icon={sm.icon} key={sm.link} />;
        })}
      </NavSocialMediaContainer>
    </>
  );
};

const NavTitle = styled.div`
  padding-top: 3px;
  color: #b382dd;
  font-family: "Comfortaa", cursive;
  font-size: 1.1em;
  letter-spacing: 2px;
  text-transform: uppercase;
`;

const NavSocialMediaContainer = styled.div``;

export default Navbar;
