import styled from "styled-components";

const NavSocialMedia = (props) => {
  return (
    <>
      <IconWrapper>
        <LinkColour href={props.link}>
          <i className={`fab fa-lg ${props.icon}`}></i>
        </LinkColour>
      </IconWrapper>
    </>
  );
};

export default NavSocialMedia;

const LinkColour = styled.a`
  color: #b382dd;
`;

const IconWrapper = styled.span`
  padding: 0 0.5em;
`;
